/*--------------------------------------------------------------
  # Hero Section
  --------------------------------------------------------------*/
#hero {
  width: 100%;
  height: 100vh;
  background-color: rgba(4, 12, 21, 0.8);
  overflow: hidden;
  position: relative;
}
#hero .carousel,
#hero .carousel-inner,
#hero .carousel-item,
#hero .carousel-item::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
#hero .carousel-item {
  background-size: cover;
  object-fit: cover;
  background-position: top center;
  background-repeat: no-repeat;
}
#hero .carousel-item::before {
  content: '';
  background-color: rgba(4, 12, 21, 0.5);
}
#hero .carousel-container {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0;
  top: 82px;
  left: 50px;
  right: 50px;
}
#hero h2 {
  color: #fff;
  margin: 0;
  font-size: 48px;
  font-weight: 700;
}
#hero p {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
  color: #fff;
  margin-top: 10px;
}

#hero .carousel-inner .carousel-item {
  transition-property: opacity;
  background-position: center top;
}
#hero .carousel-inner .carousel-item,
#hero .carousel-inner .active.carousel-item-start,
#hero .carousel-inner .active.carousel-item-end {
  opacity: 0;
}
#hero .carousel-inner .active,
#hero .carousel-inner .carousel-item-next.carousel-item-start,
#hero .carousel-inner .carousel-item-prev.carousel-item-end {
  opacity: 1;
  transition: 0.5s;
}
#hero .carousel-inner .carousel-item-next,
#hero .carousel-inner .carousel-item-prev,
#hero .carousel-inner .active.carousel-item-start,
#hero .carousel-inner .active.carousel-item-end {
  left: 0;
  transform: translate3d(0, 0, 0);
}
#hero .carousel-control-next-icon,
#hero .carousel-control-prev-icon {
  background: none;
  font-size: 30px;
  line-height: 0;
  width: auto;
  height: auto;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  transition: 0.3s;
  color: rgba(255, 255, 255, 0.5);
  width: 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#hero .carousel-control-next-icon:hover,
#hero .carousel-control-prev-icon:hover {
  background: rgba(255, 255, 255, 0.3);
  color: rgba(255, 255, 255, 0.8);
}
#hero .carousel-indicators li {
  cursor: pointer;
  background: #fff;
  overflow: hidden;
  border: 0;
  width: 12px;
  height: 12px;
  border-radius: 50px;
  opacity: 0.6;
  transition: 0.3s;
}
#hero .carousel-indicators li.active {
  opacity: 1;
  background: #ed502e;
}
#hero .btn-get-started {
  font-family: 'DIN', sans-serif;
  font-weight: 500 !important;
  font-size: 14px !important;
  letter-spacing: 1px !important;
  display: inline-block !important;
  padding: 14px 32px !important;
  border-radius: 4px !important;
  transition: 0.5s !important;
  line-height: 1 !important;
  color: #fff !important;
  -webkit-animation-delay: 0.8s !important;
  animation-delay: 0.8s !important;
  /* background: #ed502e !important; */
  margin-top: 15px !important;
}
#hero .btn-get-started:hover {
  background: #a32a29 !important;
  border: none !important;
}

#hero .member-btn {
  display: flex !important;
  justify-content: center !important;
  --width: 250px !important;
  width: var(--width) !important;
  display: block !important;
  box-sizing: border-box !important;
  padding: 20px 60px !important;
  border-radius: 999px !important;
  color: white;
  position: relative !important;
  overflow: hidden !important;
  text-transform: uppercase !important;
  border: 1px solid currentColor !important;
}

#hero .member-btn .text-container {
  display: block !important;
  position: relative !important;
  overflow: hidden !important;
}

#hero .member-btn .text {
  display: block !important;
  position: relative !important;
  font-weight: 900 !important;
  mix-blend-mode: difference !important;
}

/* #hero .member-btn:hover .text {
    animation: move-up-alternate 0.3s forwards !important;
    will-change: transform !important;
  } */

@keyframes move-up-alternate {
  from {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(80%);
  }
  51% {
    transform: translateY(-80%);
  }
  100% {
    transform: translateY(0%);
  }
}
.member-btn:before,
.member-btn:after {
  --w: calc(var(--width) / 4);
  content: '';
  z-index: -1;
  height: 100%;
  width: var(--w);
  position: absolute;
  background: rgb(68, 62, 62);
  box-shadow: calc(2 * var(--w)) 0 0 0 white;
  transition: transform 0.2s;
  will-change: transform;
}
.member-btn:before {
  top: -100%;
  left: 0;
}
.member-btn:after {
  top: 100%;
  left: var(--w);
}
.member-btn:hover:before {
  transform: translateY(100%);
}
.member-btn:hover:after {
  transform: translateY(-100%);
}

